.error_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #FF6060;
  width: 100%;
  margin: auto;
}
.error_container .title_error {
  font-size: 288px;
  font-weight: 900;
  margin: 0;
}
.error_container .text_error {
  width: 872px;
  font-size: 36px;
}
.error_container .LinkError {
  width: 284px;
  color: #FF6060;
  font-size: 18px;
  margin: 100px 0;
  height: auto;
  border-bottom: #FF6060 1px solid;
}
@media screen and (max-width: 1024px) {
  .error_container .title_error {
    font-size: 96px;
    font-weight: 900;
    margin: 100px 0;
  }
  .error_container .text_error {
    width: 260px;
    font-size: 18px;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 5%;
}
.navbar a {
  width: auto;
  height: auto;
}
.navbar a .navbar-logo {
  height: 68px;
  margin-left: 0;
}
.navbar .acceuiletapropos {
  display: flex;
  gap: 80px;
  font-size: 24px;
}
.navbar .acceuiletapropos a {
  color: #ff6060;
  text-decoration: none;
  width: auto;
  height: auto;
}
.navbar .acceuiletapropos a:hover {
  text-decoration: underline;
}
.navbar .acceuiletapropos a.active, .navbar .acceuiletapropos a:active {
  text-decoration: underline;
}
@media screen and (max-width: 1024px) {
  .navbar a {
    width: 120px;
  }
  .navbar a .navbar-logo {
    display: flex;
    align-items: center;
    width: 100%;
  }
  .navbar a .navbar-logo img {
    width: 120px;
  }
  .navbar .acceuiletapropos {
    gap: 25px;
    font-size: 16px;
  }
}

.banner {
  margin: 0 5%;
  margin-bottom: 32px;
}

.banner img {
  width: 100%;
  border-radius: 25px;
  object-fit: cover;
  filter: brightness(0.6);
  height: 223px;
}

.banner-title {
  position: absolute;
  top: 150px;
  text-align: center;
  color: #fff;
  font-weight: 400;
  font-size: 48px;
  font-family: "Montserrat";
  width: 90%;
  cursor: default;
}

@media screen and (max-width: 1024px) {
  img {
    object-position: 50% 30%;
  }
  .image-height-1 img {
    height: 111px;
  }
  .banner-title {
    top: 110px;
    left: 10%;
    font-size: 24px;
    width: 217px;
    text-align: start;
  }
}
.dropdown {
  width: 100%;
  min-height: 52px;
  height: auto;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 30px 5%;
}
.dropdown .label-container {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  padding: 0 15px;
  height: 52px;
  display: flex;
  align-items: center;
  background-color: #FF6060;
  border-radius: 5px;
  justify-content: space-between;
  cursor: pointer;
}
.dropdown .label-container label {
  cursor: pointer;
}
.dropdown .description-dropdown {
  background-color: #F6F6F6;
  margin: 0;
  padding: 15px;
  border-radius: 5px;
}
.dropdown .description-dropdown-log {
  color: #FF6060;
}
.dropdown .equipements-dropdown-log {
  color: #FF6060;
  white-space: pre-line;
}

@media screen and (max-width: 1024px) {
  .dropdown {
    padding: 0 5% 5% 5%;
  }
}
.logements {
  display: flex;
  background-color: #f6f6f6;
  margin: 0 5%;
  padding: 30px 30px;
  border-radius: 25px;
  gap: 40px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
}
.logements .title {
  margin: 0 0 10px 10px;
  font-family: "Monsterrat";
  font-size: 18px;
}

@media screen and (max-width: 1000px) {
  .logements {
    height: auto;
    background-color: #fff;
    gap: 50px;
    padding: 0;
  }
}
a {
  width: 30%;
  height: 340px;
  text-decoration: none;
}
a .logement {
  width: 100%;
  color: #fff;
  display: flex;
  cursor: pointer;
  margin: 0;
  position: relative;
}
a .logement .card-img {
  width: 100%;
  height: 340px;
  object-fit: cover;
  border-radius: 10px;
  z-index: 0;
}
a .logement .title {
  position: absolute;
  bottom: 0;
  z-index: 2;
}
a .logement::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 10px;
  z-index: 1;
}

@media screen and (max-width: 1024px) {
  a {
    width: 100%;
    height: 255px;
  }
  a .logement .card-img {
    height: 255px;
  }
}
.full-content {
  margin: 0 5% 15px 5%;
  display: flex;
  justify-content: space-between;
}
.full-content .content-left h1 {
  color: #ff6060;
  font-weight: 500;
  font-size: 36px;
  margin: 0;
}
.full-content .content-left p {
  color: #ff6060;
  font-size: 18px;
  margin: 0 0 15px 0;
}
.full-content .content-right {
  color: #ff6060;
}
.full-content .content-right .host {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 10px;
}
.full-content .content-right .host .host-name {
  width: 40%;
  align-items: center;
}
.full-content .content-right .host img {
  width: 65px;
  height: 65px;
  border-radius: 50%;
}

.description-equipements {
  display: flex;
  justify-content: space-between;
  height: auto;
}

@media screen and (max-width: 1024px) {
  .full-content {
    flex-direction: column;
  }
  .full-content .content-left h1 {
    font-size: 18px;
  }
  .full-content .content-left p {
    font-size: 14px;
  }
  .full-content .content-right {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 10px;
  }
  .full-content .content-right .host {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 10px;
  }
  .full-content .content-right .host .host-name {
    align-items: center;
  }
  .full-content .content-right .host img {
    width: 65px;
    height: 65px;
    border-radius: 50%;
  }
  .full-content .content-right .stars-align {
    align-items: center;
    margin: auto 0;
  }
  .description-equipements {
    flex-direction: column;
  }
}
.slide {
  margin: 0 5%;
  position: relative;
  margin-bottom: 32px;
}
.slide .slide-location {
  width: 100%;
  margin: 0;
}
.slide .slide-location img {
  width: 100%;
  height: 415px;
  border-radius: 25px;
  object-fit: cover;
}
.slide .btn {
  position: absolute;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.slide .btn.btn-left {
  top: 40%;
  left: 2%;
}
.slide .btn.btn-right {
  top: 40%;
  right: 2%;
}
.slide .nb-position {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 350px;
  color: #fff;
}

@media screen and (max-width: 1024px) {
  .slide .slide-location img {
    height: 255px;
  }
  .slide .btn.btn-left {
    top: 45%;
    left: 2%;
  }
  .slide .btn.btn-right {
    top: 45%;
    right: 2%;
  }
  .slide .btn img {
    width: 50%;
    height: 50%;
  }
  .nb-position {
    display: none;
  }
}
.tag-content {
  display: flex;
  gap: 15px;
}
.tag-content span {
  display: flex;
  justify-content: center;
  background-color: #ff6060;
  color: #fff;
  width: 110px;
  min-height: 25px;
  height: auto;
  border-radius: 6px;
  padding: 0 10px;
  text-align: center;
  align-items: center;
}

@media screen and (max-width: 1024px) {
  .tag-content {
    gap: 10px;
    flex-wrap: wrap;
  }
  .tag-content span {
    font-size: 10px;
    width: 75px;
  }
}
ul {
  padding: 0;
}

.stars-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.stars-container .star img {
  width: 30px;
  height: 30px;
  margin-left: 5px;
}

@media screen and (max-width: 1024px) {
  .stars-container .star img {
    width: 20px;
    height: 20px;
  }
}
.footer {
  background-color: black;
  height: 210px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer figure {
  margin: 0 auto;
}
.footer .copyright {
  color: #fff;
  margin: 20px auto 0 auto;
  cursor: default;
}

@media screen and (max-width: 1024px) {
  .copyright {
    width: 123px;
    font-size: 12px;
    text-align: center;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

