.error_container{
    display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   text-align: center;
   color: #FF6060;
   width: 100%;
   margin: auto;

   .title_error{
        font-size: 288px;
        font-weight: 900;
        margin: 0;
    }
    .text_error{
        width: 872px;
        font-size: 36px;
    }

    .LinkError{
        width: 284px;
        color: #FF6060;
        font-size: 18px;
        margin: 100px 0;
        height: auto;
        border-bottom: #FF6060 1px solid;
    }

    @media screen and (max-width: 1024px){
        
        .title_error{
            font-size: 96px;
            font-weight: 900;
            margin: 100px 0;
        }
        .text_error{
            width: 260px;
            font-size: 18px;
        }
    }
}
