a {
    width: 30%;
    height: 340px;
    text-decoration: none;

    .logement {
        width: 100%;
        // height: 340px;
        color: #fff;
        display: flex;
        cursor: pointer;
        margin: 0;
        position: relative;

        .card-img {
            width: 100%;
            height: 340px;
            object-fit: cover;
            border-radius: 10px;
            z-index: 0;
        }
        
        .title {
            position: absolute;
            bottom: 0;
            z-index: 2;
        }
    }

    .logement::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
        border-radius: 10px;
        z-index: 1;
    }
}


@media screen and (max-width: 1024px){
    a {
        width: 100%;
        height: 255px;
        
        .logement .card-img {
            height: 255px;
        }
    }
}